import { Drawer, Hidden, List, ListItem, makeStyles } from '@material-ui/core';
import React, { useEffect } from 'react';
import { NavLink, useLocation } from 'react-router-dom';
import logo from '../assets/logo.png';

const drawerWidth = 400;

const useStyles = makeStyles((theme) => ({
    appBar: {
        width: `calc(100% - ${drawerWidth}px)`,
        marginLeft: drawerWidth,
    },
    drawer: {
        width: drawerWidth,
        transition: 'ease all .5s',
        [theme.breakpoints.down('md')]: {
            width: drawerWidth * 0.5,
        },
    },
    drawerPaper: {
        width: drawerWidth,
        transition: 'ease all .5s',
        [theme.breakpoints.down('md')]: {
            width: drawerWidth * 0.5,
        },
    },
    menu: {
        height: '100vh',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        '& div': {
            width: 200,
            margin: 'auto',
        },
    },
    logo: {
        textAlign: 'center',
        marginBottom: 20,
        [theme.breakpoints.down('sm')]: {
            width: drawerWidth * 0.5,
            display: 'none',
        },
    },
    listLinkText: {
        fontFamily: `Montserrat, sans-serif !important`,
        fontWeight: 400,
    },
}));

const Sidebar = ({ onMobileClose, openMobile }) => {
    const classes = useStyles();
    const location = useLocation();

    useEffect(() => {
        if (openMobile && onMobileClose) {
            onMobileClose();
        }
    }, [location.pathname]);

    const links = [
        { to: '/', label: 'About' },
        { to: '/dungeonland', label: 'Dungeonland' },
        { to: '/midnight-draw', label: 'Midnight Draw' },
        { to: '/hypercasuals', label: 'Hypercasuals' },
        { to: '/yatzy-party', label: 'Yatzy Party' },
        { to: '/dominos', label: 'Dominos' },
        { to: '/mma-federation', label: 'MMA Federation' },
    ];

    const content = (
        <div className={classes.menu}>
            <div className={classes.logo}>
                <NavLink to='/'>
                    <img src={logo} alt='dm games' />
                </NavLink>
                <List component='nav'>
                    {links.map((l, i) => (
                        <ListItem
                            component={NavLink}
                            to={l.to}
                            exact
                            activeStyle={{
                                fontWeight: 800,
                            }}
                            className={classes.listLinkText}
                            button
                            key={`nav-${i}`}
                        >
                            {l.label}
                        </ListItem>
                    ))}
                </List>
            </div>
        </div>

        // <Grid
        //     container
        //     alignContent='center'
        //     justifyContent='center'
        //     direction='column'
        //     className={classes.menu}
        // >
        //     <Grid item xs={12} className={classes.logo}>
        //         <NavLink to='/'>
        //             <img src={logo} alt='dm games' />
        //         </NavLink>
        //     </Grid>
        //     <Grid item xs={12}>
        //         <List component='nav'>
        //             {links.map((l, i) => (
        //                 <ListItem
        //                     component={NavLink}
        //                     to={l.to}
        //                     exact
        //                     activeStyle={{
        //                         fontWeight: 800,
        //                     }}
        //                     className={classes.listLinkText}
        //                     button
        //                     key={`nav-${i}`}
        //                 >
        //                     {l.label}
        //                 </ListItem>
        //             ))}
        //         </List>
        //     </Grid>
        // </Grid>
    );

    return (
        <>
            <Hidden lgUp>
                <Drawer
                    anchor='left'
                    classes={{ paper: classes.drawer }}
                    onClose={onMobileClose}
                    open={openMobile}
                    variant='temporary'
                >
                    {content}
                </Drawer>
            </Hidden>

            <Hidden smDown>
                <Drawer
                    className={classes.drawer}
                    variant='permanent'
                    classes={{
                        paper: classes.drawerPaper,
                    }}
                    anchor='left'
                >
                    {content}
                </Drawer>
            </Hidden>
        </>
    );
};

export default Sidebar;
