import {
    Grid,
    Link,
    List,
    ListItem,
    ListItemText,
    Typography,
} from '@material-ui/core';
import React from 'react';
import Project from '../components/Project';

const Dungeonland = () => {
    const list = [
        `Created new Enemies + AI's`,
        `Created new Spells for Players and Enemies`,
        `Fixed the Procedural Map Generator`,
        `New Cosmetic DLC packs`,
        `Lots of Bug Fixing`,
    ];

    return (
        <Project>
            <Grid item xs={12}>
                <div className='aspect-ratio'>
                    <iframe
                        title='dungeoland-trailer'
                        width='560'
                        height='315'
                        src='https://www.youtube.com/embed/yxM9N1xOBqQ'
                        frameBorder='0'
                        allowFullScreen
                    ></iframe>
                </div>
            </Grid>
            <Grid item xs={12}>
                <Typography variant='h1' component='h1' gutterBottom>
                    DungeonLand
                </Typography>
                <Typography gutterBottom>
                    I was one of the developers of one of the biggest brazilian
                    indie game studios, named Critical Studio. The game was
                    published by{' '}
                    <Link href='https://www.paradoxplaza.com/'>
                        Paradox Interactive
                    </Link>
                    . <br />
                    We shipped DungeonLand and we hit Top 3 on Steam selling
                    list.
                </Typography>
                <br />

                <Typography gutterBottom>Some of my tasks included:</Typography>
                <List>
                    {list.map((t, i) => (
                        <ListItem key={`task-${i}`}>
                            <ListItemText>&bull; {t}</ListItemText>
                        </ListItem>
                    ))}
                </List>
            </Grid>
        </Project>
    );
};

export default Dungeonland;
